@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

* {
  font: 'Poppins', sans-serif;
}

.container {
  width: 40rem;
  height: 50rem;
  margin: auto;
  margin-top: 1rem;
  border-radius: 1rem;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 10%, rgba(0, 0, 0, 0.5) 100%);
}

.shadow {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px inset;
}

.error-message {
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translate(-50%, -50%); /* Move o elemento de erro de volta para cima e para a esquerda para centraliz√°-lo completamente */
    color: #fff; 
    z-index: 999; /* Garante que a mensagem de erro esteja acima de outros elementos */
  }

.top-bar {
  display: flex;
  justify-content: center;
  gap: 0.875rem;
  padding-top: 3.75rem;
}

.top-bar input {
  display: flex;
  width: 23rem;
  height: 4rem;
  background: #ebfffc;
  border: none;
  outline: none;
  border-radius: 2.5rem;
  padding-left: 2.5rem;
  color: #626262;
  font-size: 1.25rem;
  font-weight: 500;
}

.cityInput {
  box-shadow: 0px 30px 60px -12px inset rgba(50, 50, 93, 0.25), 0px 18px
      36px -18px inset rgba(0, 0, 0, 0.3);
}

.search-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
}

.search-icon:hover {
  transform: rotate(-10deg);
}

.search-icon img {
  height: 3rem;
}

.weather-image {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
}

.weather-image img {
  filter: drop-shadow(3px 1px 1px #000);
	animation: float 6s ease-in-out infinite;
	img { width: 100%; height: auto; }
}

@keyframes float {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-10px);
	}
	100% {
		transform: translatey(0px);
	}
}

.weather-temp {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 7.5rem;
  font-weight: 500;
}

.weather-location {
  display: flex;
  justify-content: center;
  color: white;
  font-size: 3.75rem;
  font-weight: 400;
}

.data-container {
  margin-top: 3.125rem;
  color: white;
  display: flex;
  justify-content: center;
}

.element {
  margin: auto;
  display: flex;
  align-items: flex-start;
  gap: .75rem;
}

.element img {
  width: 3rem;
}

.data {
  font-size: 2.125rem;
  font-weight: 400;
}

.text {
  font-size: 1.25rem;
  font-weight: 400;
}

.icon {
  margin-top: .7rem;
}

.footer {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 4rem;
  background: transparent;
  box-shadow: 0px 22px 70px 4px rgba(0, 0, 0, 0.56);
}

.footer p {
  margin: .3rem;
  padding-top: .4rem;
  font-weight: 500;
  font-size: 1rem;
  color: #fff;
}

.footer a{
  text-decoration: none;
  color: #d3d3d3;
}

.chibi {
  height: 3rem;
}


/* background */
body {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100%;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
